<template>
  <section class="contact-us" id="contact">
    <v-container class="py-8 px-4">
      <v-card flat class="contact-card pa-4">
        <v-row justify="center">
          <!-- Contact Form Column -->
          <v-col cols="12" md="7" lg="6" class="contact-form-col">
            <div class="section-title mb-6">
              <h2 class="text-h4 green--text font-weight-bold mb-2">DROP A LINE</h2>
              <v-divider class="mb-6 custom-divider" />
            </div>
            
            <v-form ref="form" v-model="valid" @submit.prevent="handleSubmit">
              <v-row dense>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="formData.name"
                    label="Your Name"
                    :rules="nameRules"
                    outlined
                    dense
                    class="rounded-lg"
                    background-color="grey lighten-4"
                  />
                </v-col>
                
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="formData.email"
                    label="Email"
                    :rules="emailRules"
                    outlined
                    dense
                    type="email"
                    class="rounded-lg"
                    background-color="grey lighten-4"
                  />
                </v-col>
                
                <v-col cols="12">
                  <v-text-field
                    v-model="formData.subject"
                    label="Subject"
                    :rules="subjectRules"
                    outlined
                    dense
                    class="rounded-lg"
                    background-color="grey lighten-4"
                  />
                </v-col>
                
                <v-col cols="12">
                  <v-textarea
                    v-model="formData.message"
                    label="Message"
                    :rules="messageRules"
                    outlined
                    auto-grow
                    rows="4"
                    class="rounded-lg"
                    background-color="grey lighten-4"
                  />
                </v-col>
              </v-row>
              
              <v-btn
                :loading="loading"
                :disabled="!valid"
                color="#FAA300"
                class="send-btn white--text px-8 mt-4"
                height="44"
                rounded
                elevation="2"
                type="submit"
              >
                <v-icon left>mdi-send</v-icon>
                Send Message
              </v-btn>
            </v-form>
          </v-col>

          <!-- Contact Info Column -->
          <v-col cols="12" md="5" lg="4" offset-lg="1" class="contact-info-col">
            <div class="section-title mb-6">
              <h2 class="text-h4 green--text font-weight-bold mb-2">CONTACT ME</h2>
              <v-divider class="mb-6 custom-divider" />
            </div>

            <div class="contact-details pa-4 rounded-lg">
              <div class="mb-6">
                <h3 class="text-h6 font-weight-bold mb-2">Marry Joy Bersabal</h3>
                <div class="contact-item d-flex align-center mb-2">
                  <v-icon color="green" class="mr-2">mdi-phone</v-icon>
                  <span>+63 946 654 8239</span>
                </div>
                <div class="contact-item d-flex align-center">
                  <v-icon color="green" class="mr-2">mdi-email</v-icon>
                  <a href="mailto:joybersabal12@gmail.com" class="green--text">joybersabal12@gmail.com</a>
                </div>
              </div>

              <div class="social-links">
                <v-btn
                  v-for="social in socialLinks"
                  :key="social.name"
                  :href="social.url"
                  target="_blank"
                  rel="noopener noreferrer"
                  class="social-btn mr-2 mb-2"
                  small
                  fab
                  :color="social.color"
                  elevation="2"
                >
                  <v-icon>{{ social.icon }}</v-icon>
                </v-btn>
              </div>

              <v-btn
                href="#"
                color="#FAA300"
                class="resume-btn white--text mt-6 px-6"
                elevation="2"
                rounded
                block
                @click="downloadResume"
              >
                <v-icon left>mdi-download</v-icon>
                Download Resume
              </v-btn>
            </div>
          </v-col>
        </v-row>

      
      </v-card>
    </v-container>
  </section>
</template>

<script>
export default {
  name: 'ContactSection',
  data: () => ({
    valid: false,
    loading: false,
    formData: {
      name: '',
      email: '',
      subject: '',
      message: ''
    },
    nameRules: [
      v => !!v || 'Name is required',
      v => v.length <= 50 || 'Name must be less than 50 characters'
    ],
    emailRules: [
      v => !!v || 'Email is required',
      v => /.+@.+\..+/.test(v) || 'Email must be valid'
    ],
    subjectRules: [
      v => !!v || 'Subject is required'
    ],
    messageRules: [
      v => !!v || 'Message is required',
      v => v.length >= 10 || 'Message must be at least 10 characters'
    ],
    socialLinks: [
      {
        name: 'Facebook',
        url: 'https://www.facebook.com/yams.bersabal',
        icon: 'mdi-facebook',
        color: '#1877F2'
      },
      {
        name: 'LinkedIn',
        url: 'https://www.linkedin.com/in/marry-joy-bersabal-2ba726332/',
        icon: 'mdi-linkedin',
        color: '#0A66C2'
      },
      {
        name: 'GitHub',
        url: 'https://github.com/MarryJoyBersabal',
        icon: 'mdi-github',
        color: '#24292F'
      }
    ]
  }),
  methods: {
    async handleSubmit() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        // Add your form submission logic here
        await new Promise(resolve => setTimeout(resolve, 1000));
        this.loading = false;
        // Reset form after successful submission
        this.$refs.form.reset();
      }
    },
    downloadResume() {
      // Add your resume download logic here
      console.log('Downloading resume...');
    }
  }
}
</script>

<style scoped>
.contact-us {
  background-color: #6E7271;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.contact-card {
  background-color: transparent !important;
}

.custom-divider {
  width: 60px;
  border-width: 3px;
  border-color: #FAA300;
}

.contact-form-col, .contact-info-col {
  transition: all 0.3s ease;
}

.contact-details {
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.social-btn {
  transition: transform 0.3s ease;
}

.social-btn:hover {
  transform: translateY(-3px);
}

.resume-btn {
  transition: all 0.3s ease;
}

.resume-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(250, 163, 0, 0.3);
}

.send-btn {
  transition: all 0.3s ease;
}

.send-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(250, 163, 0, 0.3);
}

.copyright-text {
  font-size: 0.875rem;
  opacity: 0.9;
}

/* Responsive Styles */
@media (max-width: 960px) {
  .contact-info-col {
    margin-top: 2rem;
  }
  
  .section-title {
    text-align: center;
  }
  
  .custom-divider {
    margin: 0 auto;
  }
}

@media (max-width: 600px) {
  .contact-us {
    padding: 2rem 0;
  }

  .section-title .text-h4 {
    font-size: 1.5rem !important;
  }

  .contact-details {
    padding: 1rem !important;
  }

  .social-btn {
    margin-right: 0.5rem !important;
  }

  .v-btn {
    width: 100%;
    margin-right: 0 !important;
  }

  .copyright-text {
    font-size: 0.75rem;
  }
}

/* Dark mode considerations */
@media (prefers-color-scheme: dark) {
  .contact-details {
    background-color: rgba(0, 0, 0, 0.2);
  }
}
</style>