<template>
    <div>
      <v-app-bar app color="#a7d5e1" class="nav-bar">
        <v-app-bar-nav-icon @click.stop="drawer = !drawer" class="d-md-none"></v-app-bar-nav-icon>
        
        <v-app-bar-title>
          <router-link to="/" class="text-decoration-none white--text nav-title">Bersabal.M</router-link>
        </v-app-bar-title>
        <v-spacer></v-spacer>
  
        <!-- Navigation for larger screens -->
        <v-btn text class="d-none d-md-inline-block nav-btn" @click="scrollTo('#index')">Home</v-btn>
        <v-btn text class="d-none d-md-inline-block nav-btn" @click="scrollTo('#about')">About</v-btn>
        <v-btn text class="d-none d-md-inline-block nav-btn" @click="scrollTo('#portfolio')">Portfolio</v-btn>
        <v-btn text class="d-none d-md-inline-block nav-btn" @click="scrollTo('#contact')">Contact</v-btn>
      </v-app-bar>
  
      <!-- Burger menu for smaller screens -->
      <v-navigation-drawer v-model="drawer" app temporary right class="d-md-none custom-drawer">
        <v-list nav dense>
          <v-list-item v-for="(item, index) in menuItems" :key="index" link @click="handleItemClick(item.section)">
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-navigation-drawer>
    </div>
  </template>
  
  <script>
  export default {
    name: 'NavBar',
    data() {
      return {
        drawer: false,
        menuItems: [
          { title: 'Home', section: '#index', icon: 'mdi-home' },
          { title: 'About', section: '#about', icon: 'mdi-account' },
          { title: 'Portfolio', section: '#portfolio', icon: 'mdi-briefcase' },
          { title: 'Contact', section: '#contact', icon: 'mdi-phone' }
        ]
      };
    },
    methods: {
      scrollTo(section) {
        const element = document.querySelector(section);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      },
      handleItemClick(section) {
        this.scrollTo(section);
        this.drawer = false;
      }
    }
  };
  </script>
  
  <style scoped>
  
  * {
    color: rgb(20, 20, 20);
  }
  
  .nav-bar {
    background-color: #f7f2f6 !important;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
  }
  
  .v-app-bar-title {
    font-weight: bold;
    margin-left: 10px;
    font-family: 'Times New Roman', serif;
  }
  
  .nav-btn {
    margin-right: 10px;
    color: rgb(22, 22, 22);
    font-family: 'Arial', sans-serif;
    transition: color 0.3s ease, transform 0.3s ease;
  }
  
  .nav-btn:hover {
    color: #ffcc00; /* Yellow hover color */
    transform: scale(1.05);
  }
  
  .custom-drawer {
    background-color: rgb(243, 235, 242) !important; /* Custom background color */
    border-radius: 10px;
  }
  
  .v-list-item-title {
    color: #0f0f0f !important; /* Set the text color */
  }
  
  .v-list-item {
    transition: background-color 0.3s ease;
  }
  
  .v-list-item:hover {
    background-color: #ffcc00 !important; /* Yellow hover effect for drawer items */
  }
  
  .v-list-item-icon {
    color: #666;
  }
  
  .v-list-item:hover .v-list-item-icon {
    color: #000;
  }
  
  /* Styling for links */
  .nav-title {
    transition: color 0.3s ease;
  }
  
  .nav-title:hover {
    color: #ffcc00;
  }
  
  </style>
  